@media (orientation: portrait) {
  body {
    font-size: 17px;

    display: block;
  }

  input[type="text"], input[type="button"] {
    font-size: inherit;
  }

  .widjet {
    min-width: 0px;
    border: none;
    box-shadow: none;

    padding: 10px;

    .inputMenu {
      border: none;
      box-shadow: none;
      height: 55px;
      font-size: 20px;
      padding: 0;

      input[type="text"] {
        width: 80%;
      }

      input[type="button"] {
        width: 20%;
      }
    }

    .tasks {
      padding: 0;

      .task {
        padding: 20px;
        height: 55px;
        font-size: 20px;

        input[type="text"] {
          width: 100%;
        }

        input[type="button"] {
          height: 50px;
          width: 50px;
        }
      }
    }
  }
}